@import url(https://fonts.googleapis.com/css2?family=Russo+One&display=swap);
html,body,#root{
    height:100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App,.header {
  height:96.2%;
}
nav{
    display:flex;
    align-items:center;
}
.mine {
    width:50%;
    background-color:lightblue;
    display:flex
}
.dealer {
    width: 50%;
    background-color: lightblue;
    display: flex;
    overflow: auto;
}
.infoDisplayer{
    width: 50%;
    background-color: whitesmoke
}
.gameActions{
    width: 50%;
    background-color: whitesmoke
}
.rows{
    height:50%;
    width:100%;
    display:flex;
}
.titulo{
    margin:.55rem auto;
    display:inline-block;
}
.reglas{
    position:absolute;
    right:4rem;
}
.inside-reglas {
    box-shadow: 0 0 18px rgb(0 0 0 / 15%);
    padding: .8rem;
}
.pure-menu-children {
    left:-315px!important
}
@media screen and (max-width:412px){
    .mine,.dealer{
        flex-wrap:wrap;
        align-content:stretch;
    }
    .reglas {
        display:none!important;
    }
}
.interfaceStyle_container__1eQpU {
    box-shadow: 0 0 15px grey;
    background-size: cover;
    background-position: center;
    width: 180px;
    color: #000;
    margin: .95rem;
    border-radius: 8%;
    border: 2px solid black;
}
.interfaceStyle_container__1eQpU:hover{
    cursor:pointer;
    transform:scale(1.02);
    transition-duration:.4s
}
.interfaceStyle_cardValue__1GbwS {
    margin: .2rem;
    color: red;
    font-weight: 700;
    font-size: xx-large;
    text-shadow:0 0 2.5px white;
}
.interfaceStyle_smallImage__2g0Cc{
    max-width:100%
}
.gameActions_sumDisplayer__2NGHm {
    display: flex;
}
.gameActions_col__1zNlO {
    display:inline-block;
    margin:auto 1.4rem;
    font-size:1.55rem;
}
.gameActions_container__2CuvO {
    text-align: center;
    font-family: 'Russo One', sans-serif;
}
.gameActions_buttonRow__1Fftz {
    display: flex;
    margin: 1.2rem auto;
    place-content: space-evenly
}
.gameActions_pure-button-secondary__23gJw {
    background: rgb(66, 184, 221);
    /* this is a light blue */
}
.gameActions_pure-button__t1B4w {
    font-weight:bold;
}
h3{
    font-size:1.95rem
}
.displayerStyles_container__2VjhO{
    display:flex;
    padding:1.5rem .9rem;
}
.displayerStyles_imageCol__rQc2e{
    width:60%
}

.displayerStyles_descriptionCol__2rK2Z{
    width:40%;
    padding:1.1rem
}
.displayerStyles_imageVisualizador__1cYsV{
    width:100%
}
.displayerStyles_imageVisualizador__1cYsV:hover {
    cursor:pointer;
    border:.6px solid grey;
}  
@media screen and (max-width:412px){
    .displayerStyles_container__2VjhO{
        display:block;
    }
}
